import React from "react";
import styled from "styled-components";
import { theme } from "../../data/ThemeColor";
import { BsThreeDotsVertical } from "react-icons/bs";
import { createSearchParams, useLocation, useNavigate } from "react-router-dom";
import AlertDialog from "./DialogBox/AlertDialog";
import useAxios from "../../api/Axios";
import { handleError, handleSuccess } from "../../utils/Toastify";

const ClassInformation = ({ classInfo, onReload }) => {
  const location = useLocation();
  const navigate = useNavigate();
  const axios = useAxios();
  const [open, setOpen] = React.useState(false);
  const [openBox, setOpenBox] = React.useState(false);

  const handleClickedClass = () => {
    const path = location.pathname.replace("/dashboard", "/class/view");
    navigate({
      pathname: path,
      search: createSearchParams({
        id: classInfo.classId,
      }).toString(),
    });
  };

  window.onclick = (e) => {
    const flag = e.target.className.toString().includes("openClass");
    if (flag) {
      handleClickedClass();
    }
  };

  const handleAchievedClass = (val) => {
    try {
      const url = "/class/achieved?id=" + classInfo.classId;
      axios
        .patch(url, {
          achieved: val,
        })
        .then((res) => {
          onReload();
          handleSuccess(res.data);
        })
        .catch((err) => handleError(err?.response?.data?.message));
    } catch (error) {
      handleError(error?.response?.data?.message);
    } finally {
      setOpen(false);
    }
  };

  const handleDelete = () => {
    try {
      const url = "/class/delete/class?id=" + classInfo.classId;
      axios
        .delete(url)
        .then((res) => {
          onReload();
          setOpen(false);
          handleSuccess(res.data);
        })
        .catch((err) => {
          handleError(err?.response?.data?.message);
        });
    } catch (error) {
      handleError(error?.response?.data?.message);
    }
  };

  return (
    <InfoPanel className="openClass" barient={theme[classInfo.theme].color}>
      {classInfo && (
        <>
          <Title className="openClass">{classInfo.courseName}</Title>
          <SubTitle className="openClass">
            {getSemester(classInfo.year)} Year {getSemester(classInfo.semester)}{" "}
            Semester
          </SubTitle>
        </>
      )}
      <SubTitle
        className="openClass"
        style={{ marginTop: "auto", fontSize: "0.89rem" }}
      >
        {" "}
        Session: {classInfo.session}
      </SubTitle>
      <SubTitle
        className="openClass"
        style={{ marginTop: "auto", fontSize: "0.85rem" }}
      >
        {classInfo.study_center}
      </SubTitle>
      <OptionIcon onClick={() => setOpen(!open)}>
        <BsThreeDotsVertical />
      </OptionIcon>

      {open && (
        <OptionItems>
          <Li
            onClick={() => {
              setOpen(false);
              setOpenBox(true);
            }}
          >
            Delete
          </Li>
          {location.pathname.toString().includes("/dashboard") && (
            <Li
              onClick={() => {
                handleAchievedClass(1);
              }}
            >
              Achieved
            </Li>
          )}
          {location.pathname.toString().includes("/achieved") && (
            <Li
              onClick={() => {
                handleAchievedClass(0);
              }}
            >
              Un-Achieve
            </Li>
          )}
        </OptionItems>
      )}
      {openBox && (
        <AlertDialog
          onReload={onReload}
          open={openBox}
          setOpen={setOpenBox}
          onConfirm={handleDelete}
          title="Delete Class"
          message="Are you sure you want to delete this Class?"
        />
      )}
    </InfoPanel>
  );
};

export default ClassInformation;

const getSemester = (num) => {
  if (num === 1) {
    return "1st";
  } else if (num === 2) {
    return "2nd";
  } else if (num === 3) {
    return "3rd";
  } else if (num === 4) {
    return "4th";
  } else {
    return "";
  }
};

const OptionIcon = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 50px;
  position: absolute;
  right: 10px;
  top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  &:hover {
    background: rgba(3, 3, 3, 0.2);
  }
`;

export const InfoPanel = styled.div`
  position: relative;
  padding: 01rem;
  width: 100%;
  border-radius: 5px;
  color: #fff;
  min-height: 11.5rem;
  background: ${(props) =>
    props.barient ? props.barient : "rgba(52, 31, 151,1.0)"};
`;

export const Title = styled.h1`
  font-size: 1.2rem;
`;

export const SubTitle = styled.h5`
  font-size: 1rem;
`;

export const OptionPanel = styled.div`
  position: absolute;
  right: 15px;
  top: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  width: 35px;
  height: 35px;
  font-size: 1.4rem;
  cursor: pointer;
  &:hover {
    background: rgba(240, 240, 240, 0.4);
  }
`;

export const OptionItems = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 45px;
  top: 15px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 3px 2px rgba(3, 3, 3, 0.2);
  padding: 0.5rem 0;
`;

export const Li = styled.li`
  color: #333;
  padding: 0.3rem 1rem;
  list-style-type: none;
  font-weight: 500;
  cursor: pointer;
  font-size: 1rem;

  &:hover {
    background: rgba(20, 200, 200, 0.2);
  }

  &:nth-last-child(1) {
  }
`;
